@import url(https://fonts.googleapis.com/css?family=Bangers&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@-webkit-keyframes rotation {
  to {
    -webkit-transform: rotate(360deg) translateX(-50%) translateY(-50%);
    transform: rotate(360deg) translateX(-50%) translateY(-50%);
  }
}
@keyframes rotation {
  to {
    -webkit-transform: rotate(360deg) translateX(-50%) translateY(-50%);
    transform: rotate(360deg) translateX(-50%) translateY(-50%);
  }
}
#hideMe {
  width: 100%;
  height: 100vh;
  color: #e1dee9;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-family: 'Bangers', cursive;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  /* Firefox */
  -webkit-animation: hideAnimation 2s ease-in 1s forwards;
  /* Safari and Chrome */
  /* Opera */
  animation: hideAnimation 2s ease-in 1s forwards;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
#hideMe:before {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: rotate(0) translateX(-50%) translateY(-50%);
  transform: rotate(0) translateX(-50%) translateY(-50%);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  width: 500%;
  height: 1100%;
  background: repeating-conic-gradient(#0075f2, #85c7f2 1%);
  content: '';
  -webkit-animation: rotation 20s infinite linear;
  animation: rotation 20s infinite linear;
}

.loading-text {
  position: relative;
  z-index: 2;
  font-size: 10em;
  letter-spacing: 15px;
  text-transform: uppercase;
  -webkit-transform: rotate(-10deg);
  transform: rotate(-10deg);
  text-shadow: 1px 1px #2e96ff, 2px 2px #007fff, 3px 3px #0074e8, 4px 4px #0068d1, 5px 5px #005dba,
    6px 6px #0051a3, 7px 7px #00468c, 8px 8px #003a74, 9px 9px #002f5d, 10px 10px #002346,
    10px 10px 30px rgba(0, 0, 0, 0.7);
}

.loading-text-2 {
  z-index: 4;
  font-size: 1.9em;
  text-transform: uppercase;
  -webkit-transform: rotate(-10deg);
  transform: rotate(-10deg);
  text-shadow: 1px 1px #2e96ff, 2px 2px #007fff, 3px 3px #0074e8, 4px 4px #0068d1, 5px 5px #005dba,
    6px 6px #0051a3, 7px 7px #00468c, 8px 8px #003a74, 9px 9px #002f5d, 10px 10px #002346,
    10px 10px 30px rgba(0, 0, 0, 0.7);
  margin-top: -35px;
}

@keyframes hideAnimation {
  to {
    opacity: 0;
    display: none;
  }
}
@-webkit-keyframes hideAnimation {
  to {
    opacity: 0;
    visibility: hidden;
    display: none;
  }
}

.topbar {
  padding-top: 2.5%;
}

.skills-bar {
  padding-top: 2.5%;
}

.skills-bar-centered {
  padding-top: 2.5%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.message-text {
  margin-top: 5.5%;
  font-size: 0.85em;
}

.logo-initial {
  font-weight: 800;
  font-size: 1.2em;
  font-family: Montserrat, monospace;
}

.get-in-touch-button {
  background-color: #0075f2;
  text-transform: uppercase;
}

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.profile-header {
  padding-top: 1%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.profile-header-centered {
  padding-top: 1%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.headshot {
  width: 200px;
  height: 200px;
  border-radius: 5px;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.49);
}
.name-label {
  font-size: 3.3em;
}

.description-label {
  font-size: 0.85em;
}

.description-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.location-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.profile-description {
  font-size: 0.75em;
  white-space: pre-wrap;
}

.social-bar {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.social-bar-centered {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.social-icon {
  margin: 0px 6px 0px 6px;
}

.skill-card-container {
  height: 15vh;
  width: 7.3vw;
  margin: 0px 6px 0px 6px;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.29);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
}

.skill-card-container:first-child {
  margin: 0px 6px 0px 0px;
}

.skill-image-holder-1 {
  padding-top: 10%;
  height: 60%;
  width: 50%;
}

.skill-image-holder-2 {
  padding-top: 10%;
  height: 60%;
  width: 60%;
}

.skill-label {
  font-weight: 200;
}

.profile-container {
  background-color: rgba(239, 239, 239, 1);
  height: 100vh;
}

